'use client'

import Link from 'next/link'
import { useState, useEffect, Suspense } from 'react'
import { useSearchParams, useRouter } from 'next/navigation'
import { 
  Zap, 
  BookOpen,
  Key,
  CreditCard,
  Server,
  Webhook,
  Bot,
  AlertCircle,
  ChevronRight,
  Copy,
  Check,
  ExternalLink,
  Rocket,
  Globe,
  Package
} from 'lucide-react'

const sidebarItems = [
  { id: 'overview', label: 'Overview', icon: BookOpen },
  { id: 'sdk', label: 'SDK', icon: Package },
  { id: 'authentication', label: 'Authentication', icon: Key },
  { id: 'payments', label: 'Payments', icon: CreditCard },
  { id: 'metered', label: 'Metered Endpoints', icon: Server },
  { id: 'x402jobs', label: 'x402jobs Onboard', icon: Rocket },
  { id: 'where-to-use', label: 'Where to Use', icon: Globe },
  { id: 'webhooks', label: 'Webhooks', icon: Webhook },
  { id: 'x402', label: 'AI Agents (x402)', icon: Bot },
  { id: 'errors', label: 'Errors', icon: AlertCircle },
]

function DocsPageContent() {
  const searchParams = useSearchParams()
  const router = useRouter()
  const [activeSection, setActiveSection] = useState('overview')
  const [copiedIndex, setCopiedIndex] = useState<number | null>(null)

  // Read section from URL on mount and when searchParams change
  useEffect(() => {
    const section = searchParams.get('section') || 'overview'
    // Map URL-friendly names to section IDs
    const sectionMap: Record<string, string> = {
      'x402jobs-onboard': 'x402jobs',
      'where-to-use': 'where-to-use',
      'ai-agents': 'x402',
    }
    const mappedSection = sectionMap[section] || section
    if (sidebarItems.some(item => item.id === mappedSection)) {
      setActiveSection(mappedSection)
    }
  }, [searchParams])

  const handleSectionChange = (sectionId: string) => {
    setActiveSection(sectionId)
    // Map section IDs to URL-friendly names
    const urlMap: Record<string, string> = {
      'x402jobs': 'x402jobs-onboard',
      'where-to-use': 'where-to-use',
      'x402': 'ai-agents',
    }
    const urlSection = urlMap[sectionId] || sectionId
    router.push(`/docs?section=${urlSection}`, { scroll: false })
  }

  const copyToClipboard = (text: string, index: number) => {
    navigator.clipboard.writeText(text)
    setCopiedIndex(index)
    setTimeout(() => setCopiedIndex(null), 2000)
  }

  return (
    <div className="min-h-screen bg-gray-950 text-white">
      {/* Navigation */}
      <nav className="sticky top-0 z-50 border-b border-gray-800 bg-gray-950/80 backdrop-blur-xl">
        <div className="max-w-7xl mx-auto px-4">
          <div className="flex items-center justify-between h-16">
            <Link href="/" className="flex items-center gap-2">
              <div className="w-8 h-8 rounded-lg bg-lime-400 flex items-center justify-center">
                <Zap className="w-5 h-5 text-gray-900" />
              </div>
              <span className="text-lg font-bold text-white">NexFlow</span>
              <span className="text-gray-500 ml-2">/ Docs</span>
            </Link>
            <div className="flex items-center gap-3">
              <Link href="/developers" className="px-4 py-2 text-sm text-gray-300 hover:text-white transition-colors">
                Developers
              </Link>
              <Link href="/start" className="px-4 py-2 text-sm bg-lime-400 hover:bg-lime-300 text-gray-900 font-semibold rounded-lg transition-colors">
                Start Building
              </Link>
            </div>
          </div>
        </div>
      </nav>

      <div className="max-w-7xl mx-auto flex">
        {/* Sidebar */}
        <aside className="w-64 border-r border-gray-800 min-h-[calc(100vh-4rem)] sticky top-16 overflow-y-auto">
          <nav className="p-4 space-y-1">
            {sidebarItems.map(item => (
              <button
                key={item.id}
                onClick={() => handleSectionChange(item.id)}
                className={`w-full flex items-center gap-3 px-3 py-2 rounded-lg text-left text-sm transition-colors ${
                  activeSection === item.id
                    ? 'bg-lime-400/10 text-lime-400 border border-lime-400/30'
                    : 'text-gray-400 hover:text-white hover:bg-gray-900'
                }`}
              >
                <item.icon className="w-4 h-4" />
                {item.label}
              </button>
            ))}
          </nav>
        </aside>

        {/* Main Content */}
        <main className="flex-1 p-8 max-w-4xl">
          {activeSection === 'overview' && (
            <div>
              <h1 className="text-3xl font-bold mb-6">API Overview</h1>
              
              <div className="prose prose-invert max-w-none">
                <h2 className="text-xl font-semibold text-white mt-8 mb-4">Base URL</h2>
                <div className="bg-gray-900 rounded-lg p-4 font-mono text-lime-400 border border-gray-800">
                  https://api.nexflowapp.app/api/v1
                </div>

                <h2 className="text-xl font-semibold text-white mt-8 mb-4">Quickstart Steps</h2>
                <ol className="space-y-4 text-gray-300">
                  <li className="flex gap-4">
                    <span className="w-6 h-6 rounded-full bg-lime-400 text-gray-900 flex items-center justify-center text-sm font-bold shrink-0">1</span>
                    <div>
                      <strong className="text-white">Get your API key</strong>
                      <p className="text-gray-400 text-sm mt-1">Sign up and grab your API key from the dashboard.</p>
                    </div>
                  </li>
                  <li className="flex gap-4">
                    <span className="w-6 h-6 rounded-full bg-lime-400 text-gray-900 flex items-center justify-center text-sm font-bold shrink-0">2</span>
                    <div>
                      <strong className="text-white">Create a metered endpoint</strong>
                      <p className="text-gray-400 text-sm mt-1">Define your upstream URL and pricing.</p>
                    </div>
                  </li>
                  <li className="flex gap-4">
                    <span className="w-6 h-6 rounded-full bg-lime-400 text-gray-900 flex items-center justify-center text-sm font-bold shrink-0">3</span>
                    <div>
                      <strong className="text-white">Route traffic through NexFlow</strong>
                      <p className="text-gray-400 text-sm mt-1">Use the metered URL instead of your direct API.</p>
                    </div>
                  </li>
                  <li className="flex gap-4">
                    <span className="w-6 h-6 rounded-full bg-lime-400 text-gray-900 flex items-center justify-center text-sm font-bold shrink-0">4</span>
                    <div>
                      <strong className="text-white">Receive payments</strong>
                      <p className="text-gray-400 text-sm mt-1">USDC settles to your wallet automatically.</p>
                    </div>
                  </li>
                </ol>

                <h2 className="text-xl font-semibold text-white mt-8 mb-4">End-to-End Example</h2>
                <p className="text-gray-400 mb-4">
                  An AI agent making a metered API call with x-payment header:
                </p>
                <div className="rounded-lg overflow-hidden bg-gray-900 border border-gray-800">
                  <div className="px-4 py-2 bg-gray-800 border-b border-gray-700">
                    <span className="text-sm text-gray-400 font-mono">Agent making paid API call</span>
                  </div>
                  <pre className="p-4 overflow-x-auto text-sm">
                    <code className="text-gray-300 font-mono">{`curl https://api.nexflowapp.app/api/v1/metered/ep_abc123 \\
  -H "Authorization: Bearer sk_live_xxx" \\
  -H "x-payment: 0x7f8a...3b2c" \\
  -H "Content-Type: application/json" \\
  -d '{"prompt": "Explain quantum computing"}'

# Response
{
  "data": {
    "completion": "Quantum computing uses..."
  },
  "x402": {
    "verified": true,
    "facilitator": "cdp",
    "amount": "1000",
    "token": "USDC",
    "network": "base"
  },
  "usage": {
    "id": "usage_xyz",
    "units": 1,
    "cost": "0.001"
  }
}`}</code>
                  </pre>
                </div>
              </div>
            </div>
          )}

          {activeSection === 'sdk' && (
            <div>
              <h1 className="text-3xl font-bold mb-6">SDK</h1>
              
              <div className="prose prose-invert max-w-none">
                <p className="text-gray-400 mb-6">
                  The official TypeScript/JavaScript SDK for NexFlow metered endpoints.
                </p>

                <h2 className="text-xl font-semibold text-white mt-8 mb-4">Installation</h2>
                <div className="rounded-lg overflow-hidden bg-gray-900 border border-gray-800">
                  <div className="px-4 py-2 bg-gray-800 border-b border-gray-700">
                    <span className="text-sm text-gray-400 font-mono">npm / yarn / pnpm</span>
                  </div>
                  <pre className="p-4 overflow-x-auto text-sm">
                    <code className="text-gray-300 font-mono">{`npm install nexflow-sdk
# or
yarn add nexflow-sdk
# or
pnpm add nexflow-sdk`}</code>
                  </pre>
                </div>

                <h2 className="text-xl font-semibold text-white mt-8 mb-4">Quick Start</h2>
                <div className="rounded-lg overflow-hidden bg-gray-900 border border-gray-800">
                  <div className="px-4 py-2 bg-gray-800 border-b border-gray-700">
                    <span className="text-sm text-gray-400 font-mono">TypeScript</span>
                  </div>
                  <pre className="p-4 overflow-x-auto text-sm">
                    <code className="text-gray-300 font-mono">{`import { NexFlow } from 'nexflow-sdk';

const nf = new NexFlow({
  apiKey: process.env.NEXFLOW_API_KEY!
});

// Create a metered endpoint
const { endpoint } = await nf.endpoints.create({
  name: 'My API',
  upstreamUrl: 'https://api.example.com/v1/data',
  pricePerCall: '0.001', // USDC per call
  recipientAddress: '0xYourWallet...'
});

// Run a metered call
const result = await nf.run(endpoint.id, { query: 'hello' });
console.log(result.data);
console.log(\`Charged: $\${result.billing.pricePerCall} USDC\`);`}</code>
                  </pre>
                </div>

                <h2 className="text-xl font-semibold text-white mt-8 mb-4">Available Methods</h2>
                <div className="space-y-3">
                  <div className="p-4 rounded-lg bg-gray-900 border border-gray-800">
                    <code className="text-lime-400 font-mono">nf.endpoints.create(request)</code>
                    <p className="text-gray-500 text-sm mt-1">Create a new metered endpoint</p>
                  </div>
                  <div className="p-4 rounded-lg bg-gray-900 border border-gray-800">
                    <code className="text-lime-400 font-mono">nf.endpoints.list()</code>
                    <p className="text-gray-500 text-sm mt-1">List all endpoints for your account</p>
                  </div>
                  <div className="p-4 rounded-lg bg-gray-900 border border-gray-800">
                    <code className="text-lime-400 font-mono">nf.endpoints.summary(id)</code>
                    <p className="text-gray-500 text-sm mt-1">Get billing summary for an endpoint</p>
                  </div>
                  <div className="p-4 rounded-lg bg-gray-900 border border-gray-800">
                    <code className="text-lime-400 font-mono">nf.usage.summary()</code>
                    <p className="text-gray-500 text-sm mt-1">Get usage summary (daily/monthly limits)</p>
                  </div>
                  <div className="p-4 rounded-lg bg-gray-900 border border-gray-800">
                    <code className="text-lime-400 font-mono">nf.run(endpointId, payload)</code>
                    <p className="text-gray-500 text-sm mt-1">Execute a metered call through your endpoint</p>
                  </div>
                </div>

                <h2 className="text-xl font-semibold text-white mt-8 mb-4">Error Handling</h2>
                <div className="rounded-lg overflow-hidden bg-gray-900 border border-gray-800">
                  <pre className="p-4 overflow-x-auto text-sm">
                    <code className="text-gray-300 font-mono">{`import { NexFlow, QuotaExceededError } from 'nexflow-sdk';

try {
  await nf.run('ep_xxx', payload);
} catch (error) {
  if (error instanceof QuotaExceededError) {
    console.log('Quota exceeded!');
    console.log(\`Daily: \${error.usage.daily}/\${error.limits.dailyLimit}\`);
  }
}`}</code>
                  </pre>
                </div>

                <h2 className="text-xl font-semibold text-white mt-8 mb-4">Response Types</h2>
                <p className="text-gray-400 mb-4">
                  The <code className="text-lime-400">nf.run()</code> method returns billing and usage info:
                </p>
                <div className="rounded-lg overflow-hidden bg-gray-900 border border-gray-800">
                  <pre className="p-4 overflow-x-auto text-sm">
                    <code className="text-gray-300 font-mono">{`{
  success: true,
  data: { /* upstream response */ },
  billing: {
    pricePerCall: "0.001",
    platformCut: "0.00002",
    creatorNet: "0.00098"
  },
  usage: {
    daily: 42,
    monthly: 1500,
    dailyLimit: 1000,
    monthlyLimit: 10000
  }
}`}</code>
                  </pre>
                </div>

                <div className="mt-8 p-4 rounded-lg bg-lime-400/10 border border-lime-400/30">
                  <p className="text-lime-400 text-sm">
                    <strong>ðŸ“¦ npm:</strong> <code className="bg-gray-800 px-2 py-1 rounded">npm install nexflow-sdk</code>
                  </p>
                  <p className="text-gray-400 text-sm mt-2">
                    npm: <a href="https://www.npmjs.com/package/nexflow-sdk" className="text-lime-400 hover:text-lime-300">npmjs.com/package/nexflow-sdk</a>
                  </p>
                </div>
              </div>
            </div>
          )}

          {activeSection === 'authentication' && (
            <div>
              <h1 className="text-3xl font-bold mb-6">Authentication</h1>
              
              <div className="prose prose-invert max-w-none">
                <p className="text-gray-400 mb-6">
                  NexFlow uses API keys to authenticate requests. Include your API key in the Authorization header.
                </p>

                <h2 className="text-xl font-semibold text-white mt-8 mb-4">API Key Types</h2>
                <div className="space-y-4">
                  <div className="p-4 rounded-lg bg-gray-900 border border-gray-800">
                    <div className="flex items-center gap-2 mb-2">
                      <span className="px-2 py-1 text-xs rounded bg-lime-400/20 text-lime-400">Live</span>
                      <code className="text-gray-300 font-mono">sk_live_xxx</code>
                    </div>
                    <p className="text-gray-500 text-sm">For production. Real payments on mainnet.</p>
                  </div>
                  <div className="p-4 rounded-lg bg-gray-900 border border-gray-800">
                    <div className="flex items-center gap-2 mb-2">
                      <span className="px-2 py-1 text-xs rounded bg-amber-500/20 text-amber-400">Test</span>
                      <code className="text-gray-300 font-mono">sk_test_xxx</code>
                    </div>
                    <p className="text-gray-500 text-sm">For development. Uses testnet facilitators.</p>
                  </div>
                </div>

                <h2 className="text-xl font-semibold text-white mt-8 mb-4">Usage</h2>
                <div className="rounded-lg overflow-hidden bg-gray-900 border border-gray-800">
                  <pre className="p-4 overflow-x-auto text-sm">
                    <code className="text-gray-300 font-mono">{`curl https://api.nexflowapp.app/api/v1/endpoints \\
  -H "Authorization: Bearer sk_live_xxx"`}</code>
                  </pre>
                </div>
              </div>
            </div>
          )}

          {activeSection === 'payments' && (
            <div>
              <h1 className="text-3xl font-bold mb-6">Payments</h1>
              
              <div className="prose prose-invert max-w-none">
                <p className="text-gray-400 mb-6">
                  Create payment sessions for redirect checkout or handle x402 payments for agents.
                </p>

                <h2 className="text-xl font-semibold text-white mt-8 mb-4">Create Payment Session</h2>
                <div className="rounded-lg overflow-hidden bg-gray-900 border border-gray-800">
                  <div className="px-4 py-2 bg-gray-800 border-b border-gray-700 flex items-center gap-2">
                    <span className="px-2 py-1 text-xs rounded bg-blue-500/20 text-blue-400 font-mono">POST</span>
                    <span className="text-sm text-gray-400 font-mono">/payments/sessions</span>
                  </div>
                  <pre className="p-4 overflow-x-auto text-sm">
                    <code className="text-gray-300 font-mono">{`{
  "merchantId": "merch_123",
  "amount": 50.00,
  "sourceCurrency": "USD",
  "targetCurrency": "USDC",
  "mode": "fiat",
  "country": "US",
  "successUrl": "https://yourapp.com/success",
  "cancelUrl": "https://yourapp.com/cancel"
}`}</code>
                  </pre>
                </div>

                <h2 className="text-xl font-semibold text-white mt-8 mb-4">Response</h2>
                <div className="rounded-lg overflow-hidden bg-gray-900 border border-gray-800">
                  <pre className="p-4 overflow-x-auto text-sm">
                    <code className="text-gray-300 font-mono">{`{
  "session": {
    "id": "sess_abc123",
    "status": "pending",
    "amount": 50.00,
    "expiresAt": "2025-12-11T21:00:00Z"
  },
  "route": {
    "provider": "coinbase_onramp",
    "estimatedFee": 0.75,
    "estimatedSettlement": 60
  },
  "paymentInstructions": {
    "type": "redirect",
    "redirectUrl": "https://pay.coinbase.com/..."
  }
}`}</code>
                  </pre>
                </div>
              </div>
            </div>
          )}

          {activeSection === 'metered' && (
            <div>
              <h1 className="text-3xl font-bold mb-6">Metered Endpoints</h1>
              
              <div className="prose prose-invert max-w-none">
                <p className="text-gray-400 mb-6">
                  Create metered endpoints to charge per API call, per token, or per custom unit.
                </p>

                <h2 className="text-xl font-semibold text-white mt-8 mb-4">Create Endpoint</h2>
                <div className="rounded-lg overflow-hidden bg-gray-900 border border-gray-800">
                  <div className="px-4 py-2 bg-gray-800 border-b border-gray-700 flex items-center gap-2">
                    <span className="px-2 py-1 text-xs rounded bg-blue-500/20 text-blue-400 font-mono">POST</span>
                    <span className="text-sm text-gray-400 font-mono">/endpoints</span>
                  </div>
                  <pre className="p-4 overflow-x-auto text-sm">
                    <code className="text-gray-300 font-mono">{`{
  "name": "Chat Completions",
  "upstreamUrl": "https://api.openai.com/v1/chat/completions",
  "pricePerCall": "0.001",
  "network": "base",
  "token": "USDC",
  "method": "POST"
}`}</code>
                  </pre>
                </div>

                <h2 className="text-xl font-semibold text-white mt-8 mb-4">Using the Metered URL</h2>
                <p className="text-gray-400 mb-4">
                  Once created, route traffic through the metered URL:
                </p>
                <div className="rounded-lg overflow-hidden bg-gray-900 border border-gray-800">
                  <pre className="p-4 overflow-x-auto text-sm">
                    <code className="text-gray-300 font-mono">{`# Instead of:
curl https://api.openai.com/v1/chat/completions

# Use:
curl https://api.nexflowapp.app/api/v1/metered/ep_xxx \\
  -H "x-payment: 0x..."  # USDC payment tx hash`}</code>
                  </pre>
                </div>
              </div>
            </div>
          )}

          {activeSection === 'x402jobs' && (
            <div>
              <h1 className="text-3xl font-bold mb-6">x402jobs Onboard</h1>
              
              <div className="prose prose-invert max-w-none">
                <p className="text-gray-400 mb-6">
                  One-stop onboarding API that creates a complete NexFlow metered endpoint setup in a single request. 
                  Handles endpoint creation, x402 verification, webhooks, alerts, and API key generation.
                </p>

                <div className="mb-6 p-4 rounded-lg bg-blue-500/10 border border-blue-500/30">
                  <p className="text-blue-400 text-sm">
                    <strong>ðŸ’¡ Quick Start:</strong> Use this endpoint to quickly onboard new API integrations with 
                    full payment, webhook, and monitoring setup in one API call.
                  </p>
                </div>

                <h2 className="text-xl font-semibold text-white mt-8 mb-4">Onboard Endpoint</h2>
                <div className="rounded-lg overflow-hidden bg-gray-900 border border-gray-800">
                  <div className="px-4 py-2 bg-gray-800 border-b border-gray-700 flex items-center gap-2">
                    <span className="px-2 py-1 text-xs rounded bg-blue-500/20 text-blue-400 font-mono">POST</span>
                    <span className="text-sm text-gray-400 font-mono">/api/v1/x402jobs/onboard</span>
                    <span className="px-2 py-1 text-xs rounded bg-amber-500/20 text-amber-400 ml-auto">Admin Only</span>
                  </div>
                  <div className="p-4">
                    <p className="text-gray-400 text-sm mb-3">Request Body:</p>
                    <pre className="overflow-x-auto text-sm">
                      <code className="text-gray-300 font-mono">{`{
  "targetUrl": "https://api.example.com/resource",
  "name": "My API Endpoint",
  "price": "1000000",
  "recipientAddress": "0x...",
  "webhookUrl": "https://example.com/webhook",
  "webhookEvents": ["payment.verified", "usage.recorded"],
  "alertThreshold": 100,
  "alertMetric": "http.request.count",
  "apiKeyName": "My API Key",
  "description": "Optional description"
}`}</code>
                    </pre>
                  </div>
                </div>

                <h2 className="text-xl font-semibold text-white mt-8 mb-4">Request Fields</h2>
                <div className="space-y-3">
                  <div className="p-4 rounded-lg bg-gray-900 border border-gray-800">
                    <div className="flex items-center gap-2 mb-1">
                      <code className="text-lime-400 font-mono text-sm">targetUrl</code>
                      <span className="px-2 py-0.5 text-xs rounded bg-red-500/20 text-red-400">Required</span>
                    </div>
                    <p className="text-gray-500 text-sm">The upstream API URL to proxy requests to.</p>
                  </div>
                  <div className="p-4 rounded-lg bg-gray-900 border border-gray-800">
                    <div className="flex items-center gap-2 mb-1">
                      <code className="text-lime-400 font-mono text-sm">price</code>
                      <span className="px-2 py-0.5 text-xs rounded bg-gray-700 text-gray-400">Optional</span>
                    </div>
                    <p className="text-gray-500 text-sm">Price in atomic units (default: "1000000" = 1 USDC).</p>
                  </div>
                  <div className="p-4 rounded-lg bg-gray-900 border border-gray-800">
                    <div className="flex items-center gap-2 mb-1">
                      <code className="text-lime-400 font-mono text-sm">webhookUrl</code>
                      <span className="px-2 py-0.5 text-xs rounded bg-gray-700 text-gray-400">Optional</span>
                    </div>
                    <p className="text-gray-500 text-sm">Webhook URL for event notifications (payment.verified, usage.recorded, etc.).</p>
                  </div>
                  <div className="p-4 rounded-lg bg-gray-900 border border-gray-800">
                    <div className="flex items-center gap-2 mb-1">
                      <code className="text-lime-400 font-mono text-sm">alertThreshold</code>
                      <span className="px-2 py-0.5 text-xs rounded bg-gray-700 text-gray-400">Optional</span>
                    </div>
                    <p className="text-gray-500 text-sm">Alert threshold value (requires alertMetric).</p>
                  </div>
                  <div className="p-4 rounded-lg bg-gray-900 border border-gray-800">
                    <div className="flex items-center gap-2 mb-1">
                      <code className="text-lime-400 font-mono text-sm">apiKeyName</code>
                      <span className="px-2 py-0.5 text-xs rounded bg-gray-700 text-gray-400">Optional</span>
                    </div>
                    <p className="text-gray-500 text-sm">Name for the automatically created API key.</p>
                  </div>
                </div>

                <h2 className="text-xl font-semibold text-white mt-8 mb-4">Response</h2>
                <div className="rounded-lg overflow-hidden bg-gray-900 border border-gray-800">
                  <pre className="p-4 overflow-x-auto text-sm">
                    <code className="text-gray-300 font-mono">{`{
  "success": true,
  "endpoint": {
    "id": "ep_abc123",
    "name": "My API Endpoint",
    "meteredUrl": "https://api.nexflowapp.app/api/v1/metered/ep_abc123",
    "upstreamUrl": "https://api.example.com/resource",
    "price": "1000000",
    "network": "base",
    "status": "active"
  },
  "apiKey": {
    "id": "key_xyz789",
    "name": "My API Key",
    "token": "nf_live_...",
    "role": "user",
    "rateLimit": 1000
  },
  "webhook": {
    "id": "wh_def456",
    "url": "https://example.com/webhook",
    "events": ["payment.verified", "usage.recorded"],
    "enabled": true
  },
  "alertRule": {
    "id": "ar_ghi789",
    "name": "My API Endpoint - http.request.count Alert",
    "metricName": "http.request.count",
    "threshold": 100,
    "enabled": true
  },
  "exampleCurl": "curl -X POST https://api.nexflowapp.app/api/v1/metered/ep_abc123 \\\\n  -H \\"Authorization: Bearer nf_live_...\\" \\\\n  -H \\"Content-Type: application/json\\" \\\\n  -H \\"x-payment: x402 <your_payment_header>\\" \\\\n  -d '{\\"your\\": \\"request\\", \\"body\\": \\"here\\"}'"
}`}</code>
                  </pre>
                </div>

                <h2 className="text-xl font-semibold text-white mt-8 mb-4">Example Usage</h2>
                <div className="rounded-lg overflow-hidden bg-gray-900 border border-gray-800">
                  <div className="px-4 py-2 bg-gray-800 border-b border-gray-700">
                    <span className="text-sm text-gray-400 font-mono">Complete onboarding example</span>
                  </div>
                  <pre className="p-4 overflow-x-auto text-sm">
                    <code className="text-gray-300 font-mono">{`curl -X POST https://api.nexflowapp.app/api/v1/x402jobs/onboard \\
  -H "Authorization: Bearer nf_live_admin_key" \\
  -H "Content-Type: application/json" \\
  -d '{
    "targetUrl": "https://api.openai.com/v1/chat/completions",
    "name": "OpenAI Chat Completions",
    "price": "1000000",
    "recipientAddress": "0xBBEE769932738F8c4AE4318822585820963c387C",
    "webhookUrl": "https://myapp.com/webhooks/nexflow",
    "webhookEvents": ["payment.verified", "usage.recorded"],
    "alertThreshold": 1000,
    "alertMetric": "http.request.count",
    "apiKeyName": "OpenAI Integration Key"
  }'`}</code>
                  </pre>
                </div>

                <h2 className="text-xl font-semibold text-white mt-8 mb-4">What Gets Created</h2>
                <ol className="space-y-3 text-gray-300">
                  <li className="flex gap-3">
                    <span className="w-6 h-6 rounded-full bg-lime-400 text-gray-900 flex items-center justify-center text-sm font-bold shrink-0">1</span>
                    <div>
                      <strong className="text-white">Metered Endpoint</strong>
                      <p className="text-gray-500 text-sm mt-1">Ready-to-use endpoint with x402 payment verification enabled.</p>
                    </div>
                  </li>
                  <li className="flex gap-3">
                    <span className="w-6 h-6 rounded-full bg-lime-400 text-gray-900 flex items-center justify-center text-sm font-bold shrink-0">2</span>
                    <div>
                      <strong className="text-white">API Key</strong>
                      <p className="text-gray-500 text-sm mt-1">Scoped API key for accessing the metered endpoint.</p>
                    </div>
                  </li>
                  <li className="flex gap-3">
                    <span className="w-6 h-6 rounded-full bg-lime-400 text-gray-900 flex items-center justify-center text-sm font-bold shrink-0">3</span>
                    <div>
                      <strong className="text-white">Webhook Configuration</strong>
                      <p className="text-gray-500 text-sm mt-1">Optional webhook for payment and usage events.</p>
                    </div>
                  </li>
                  <li className="flex gap-3">
                    <span className="w-6 h-6 rounded-full bg-lime-400 text-gray-900 flex items-center justify-center text-sm font-bold shrink-0">4</span>
                    <div>
                      <strong className="text-white">Alert Rules</strong>
                      <p className="text-gray-500 text-sm mt-1">Optional monitoring alerts for metrics like request count.</p>
                    </div>
                  </li>
                </ol>

                <div className="mt-8 p-4 rounded-lg bg-gray-900 border border-gray-800">
                  <div className="flex items-start gap-3">
                    <ExternalLink className="w-5 h-5 text-lime-400 shrink-0 mt-0.5" />
                    <div>
                      <p className="text-white font-medium mb-1">Full Implementation Guide</p>
                      <p className="text-gray-400 text-sm">
                        For detailed implementation notes, testing examples, and troubleshooting, see the{' '}
                        <a href="/docs/x402jobs-onboard" className="text-lime-400 hover:text-lime-300 underline">
                          complete implementation documentation
                        </a>.
                      </p>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          )}

          {activeSection === 'webhooks' && (
            <div>
              <h1 className="text-3xl font-bold mb-6">Webhooks</h1>
              
              <div className="prose prose-invert max-w-none">
                <p className="text-gray-400 mb-6">
                  Receive real-time notifications for payment and usage events.
                </p>

                <h2 className="text-xl font-semibold text-white mt-8 mb-4">Events</h2>
                <div className="space-y-3">
                  {[
                    { event: 'payment.verified', desc: 'Payment was verified by a facilitator' },
                    { event: 'payment.failed', desc: 'Payment verification failed' },
                    { event: 'usage.recorded', desc: 'Metered usage was logged' },
                    { event: 'session.completed', desc: 'Payment session completed' },
                  ].map(item => (
                    <div key={item.event} className="p-4 rounded-lg bg-gray-900 border border-gray-800">
                      <code className="text-lime-400 font-mono">{item.event}</code>
                      <p className="text-gray-500 text-sm mt-1">{item.desc}</p>
                    </div>
                  ))}
                </div>

                <h2 className="text-xl font-semibold text-white mt-8 mb-4">Payload Example</h2>
                <div className="rounded-lg overflow-hidden bg-gray-900 border border-gray-800">
                  <pre className="p-4 overflow-x-auto text-sm">
                    <code className="text-gray-300 font-mono">{`{
  "event": "payment.verified",
  "timestamp": "2025-12-11T20:30:00Z",
  "data": {
    "paymentId": "pay_abc123",
    "amount": "1000000",
    "token": "USDC",
    "network": "base",
    "facilitator": "cdp",
    "txHash": "0x7f8a...3b2c"
  }
}`}</code>
                  </pre>
                </div>
              </div>
            </div>
          )}

          {activeSection === 'where-to-use' && (
            <div>
              <h1 className="text-3xl font-bold mb-6">Where to Use Your NexFlow Endpoint</h1>
              
              <div className="prose prose-invert max-w-none">
                <p className="text-gray-400 mb-6">
                  Once you've created a NexFlow metered endpoint, you can use it in several places to monetize your API and make it discoverable to AI agents, developers, and automated systems.
                </p>

                <h2 className="text-xl font-semibold text-white mt-8 mb-4">x402jobs</h2>
                <p className="text-gray-300 mb-4">
                  x402jobs is a directory and discovery platform for x402-enabled resources. By registering your NexFlow endpoint with x402jobs, you make it discoverable to AI agents and automated systems that are looking for paid APIs.
                </p>
                <p className="text-gray-300 mb-4">
                  NexFlow handles all the x402 payment verification, metering, and logging automatically. When an agent discovers your endpoint through x402jobs and makes a call, NexFlow verifies the x402 payment header, processes the request, and logs the usageâ€”all without any additional code on your part.
                </p>

                <h3 className="text-lg font-semibold text-white mt-6 mb-3">How to Register</h3>
                <ol className="space-y-3 text-gray-300 mb-6">
                  <li className="flex gap-3">
                    <span className="w-6 h-6 rounded-full bg-gray-800 text-gray-400 flex items-center justify-center text-sm font-mono shrink-0">1</span>
                    <div>
                      <strong className="text-white">Run the NexFlow x402jobs onboarding job</strong> to wrap your endpoint using the <code className="text-lime-400">/api/v1/x402jobs/onboard</code> API.
                    </div>
                  </li>
                  <li className="flex gap-3">
                    <span className="w-6 h-6 rounded-full bg-gray-800 text-gray-400 flex items-center justify-center text-sm font-mono shrink-0">2</span>
                    <div>
                      <strong className="text-white">Copy the returned <code className="text-lime-400">nexflowEndpointUrl</code> or <code className="text-lime-400">x402ResourceUrl</code></strong> from the response.
                    </div>
                  </li>
                  <li className="flex gap-3">
                    <span className="w-6 h-6 rounded-full bg-gray-800 text-gray-400 flex items-center justify-center text-sm font-mono shrink-0">3</span>
                    <div>
                      <strong className="text-white">Paste it into the "Register Resource" field</strong> on <a href="https://x402.jobs" target="_blank" rel="noopener noreferrer" className="text-lime-400 hover:text-lime-300">x402jobs</a>.
                    </div>
                  </li>
                </ol>
                <p className="text-gray-300 mb-6">
                  Once registered, your endpoint will appear in the x402jobs directory, and agents can discover and call it. NexFlow will emit <code className="text-lime-400">payment.verified</code> and <code className="text-lime-400">usage.recorded</code> webhooks for all calls coming from x402jobs, so you can track usage and payments in real-time.
                </p>

                <h2 className="text-xl font-semibold text-white mt-8 mb-4">Agent Directories and Marketplaces</h2>
                <p className="text-gray-300 mb-4">
                  Your NexFlow endpoint can serve as the paid backend for AI agents listed in agent directories and marketplaces. Whether you're building an open-source agent or a hosted service, NexFlow provides the payment infrastructure so you can focus on the agent logic.
                </p>
                <ul className="space-y-2 text-gray-300 mb-6 list-disc list-inside">
                  <li>List your agent in AI agent directories and point it at your NexFlow metered endpoint.</li>
                  <li>Use NexFlow as the paid backend for LangChain / MCP / custom agents.</li>
                  <li>Monetize agent-to-agent communication by routing inter-agent API calls through NexFlow endpoints.</li>
                  <li>Enable pay-per-use agent features by wrapping expensive operations (LLM calls, data processing, external API calls) with NexFlow metering.</li>
                </ul>

                <h2 className="text-xl font-semibold text-white mt-8 mb-4">Public API Listings</h2>
                <p className="text-gray-300 mb-4">
                  You can list your NexFlow-wrapped endpoint in public API directories, GitHub "awesome APIs" lists, and developer resource collections. All calls to your listed endpoint will automatically go through NexFlow's metering and x402 verification, ensuring you get paid for usage while maintaining a simple, standard API interface.
                </p>
                <p className="text-gray-300 mb-4">
                  When listing your endpoint, include the NexFlow metered URL (not your upstream URL) so that all traffic is properly metered and payments are verified. NexFlow handles the complexity of payment verification, so developers using your API only need to include an x402 payment headerâ€”no additional authentication or payment setup required.
                </p>

                <h3 className="text-lg font-semibold text-white mt-6 mb-3">What to Include in Your Listing</h3>
                <ul className="space-y-2 text-gray-300 mb-6 list-disc list-inside">
                  <li><strong className="text-white">Name:</strong> A clear, descriptive name for your API</li>
                  <li><strong className="text-white">Short description:</strong> What your API does and what problem it solves</li>
                  <li><strong className="text-white">Base URL:</strong> Your NexFlow metered endpoint URL (e.g., <code className="text-lime-400">https://api.nexflowapp.app/api/v1/metered/ep_xxx</code>)</li>
                  <li><strong className="text-white">Authentication:</strong> API key + x402 payment header</li>
                  <li><strong className="text-white">Documentation link:</strong> Link to your API docs or the NexFlow documentation</li>
                  <li><strong className="text-white">Example request:</strong> Show how to call your endpoint with the x402 payment header</li>
                </ul>

                <div className="mt-8 p-4 rounded-lg bg-gray-900 border border-gray-800">
                  <h3 className="text-lg font-semibold text-white mb-3">Example Listing Format</h3>
                  <pre className="overflow-x-auto text-sm">
                    <code className="text-gray-300 font-mono">{`## My API Name

**Description**: Brief description of what your API does.

**Base URL**: \`https://api.nexflowapp.app/api/v1/metered/ep_xxx\`

**Authentication**: 
- Header: \`Authorization: Bearer YOUR_API_KEY\`
- Header: \`X-Payment: x402 <payment_header>\`

**Documentation**: [Link to your docs](https://your-docs.com)`}</code>
                  </pre>
                </div>

                <h2 className="text-xl font-semibold text-white mt-8 mb-4">Next Steps</h2>
                <div className="space-y-3">
                  <div className="p-4 rounded-lg bg-gray-900 border border-gray-800 flex items-start gap-3">
                    <ChevronRight className="w-5 h-5 text-lime-400 shrink-0 mt-0.5" />
                    <div>
                      <strong className="text-white">Create your first metered endpoint:</strong>
                      <p className="text-gray-400 text-sm mt-1">Follow the Quickstart guide to create a NexFlow endpoint in minutes.</p>
                    </div>
                  </div>
                  <div className="p-4 rounded-lg bg-gray-900 border border-gray-800 flex items-start gap-3">
                    <ChevronRight className="w-5 h-5 text-lime-400 shrink-0 mt-0.5" />
                    <div>
                      <strong className="text-white">Onboard with x402jobs:</strong>
                      <p className="text-gray-400 text-sm mt-1">Use the x402jobs onboarding API to register your endpoint with x402jobs.</p>
                    </div>
                  </div>
                  <div className="p-4 rounded-lg bg-gray-900 border border-gray-800 flex items-start gap-3">
                    <ChevronRight className="w-5 h-5 text-lime-400 shrink-0 mt-0.5" />
                    <div>
                      <strong className="text-white">Set up webhooks:</strong>
                      <p className="text-gray-400 text-sm mt-1">Configure webhooks to receive payment.verified and usage.recorded events for real-time monitoring.</p>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          )}

          {activeSection === 'x402' && (
            <div>
              <h1 className="text-3xl font-bold mb-6">AI Agents (x402)</h1>
              
              <div className="prose prose-invert max-w-none">
                <p className="text-gray-400 mb-6">
                  The x402 protocol enables AI agents to make paid API calls autonomously.
                </p>

                <h2 className="text-xl font-semibold text-white mt-8 mb-4">How It Works</h2>
                <ol className="space-y-4 text-gray-300">
                  <li className="flex gap-4">
                    <span className="w-6 h-6 rounded-full bg-gray-800 text-gray-400 flex items-center justify-center text-sm font-mono shrink-0">1</span>
                    <div>
                      <strong className="text-white">Agent calls metered endpoint without payment</strong>
                      <p className="text-gray-500 text-sm mt-1">Receives 402 Payment Required with x402 challenge.</p>
                    </div>
                  </li>
                  <li className="flex gap-4">
                    <span className="w-6 h-6 rounded-full bg-gray-800 text-gray-400 flex items-center justify-center text-sm font-mono shrink-0">2</span>
                    <div>
                      <strong className="text-white">Agent parses the challenge</strong>
                      <p className="text-gray-500 text-sm mt-1">Extracts network, token, amount, and recipient.</p>
                    </div>
                  </li>
                  <li className="flex gap-4">
                    <span className="w-6 h-6 rounded-full bg-gray-800 text-gray-400 flex items-center justify-center text-sm font-mono shrink-0">3</span>
                    <div>
                      <strong className="text-white">Agent makes USDC payment</strong>
                      <p className="text-gray-500 text-sm mt-1">Sends exact amount to the specified address.</p>
                    </div>
                  </li>
                  <li className="flex gap-4">
                    <span className="w-6 h-6 rounded-full bg-lime-400 text-gray-900 flex items-center justify-center text-sm font-mono shrink-0">4</span>
                    <div>
                      <strong className="text-white">Agent retries with x-payment header</strong>
                      <p className="text-gray-500 text-sm mt-1">Includes tx hash. NexFlow verifies and returns data.</p>
                    </div>
                  </li>
                </ol>

                <h2 className="text-xl font-semibold text-white mt-8 mb-4">402 Challenge Response</h2>
                <div className="rounded-lg overflow-hidden bg-gray-900 border border-gray-800">
                  <pre className="p-4 overflow-x-auto text-sm">
                    <code className="text-gray-300 font-mono">{`HTTP/1.1 402 Payment Required
Content-Type: application/json

{
  "status": 402,
  "x402": {
    "version": "1",
    "accepts": [{
      "scheme": "exact",
      "network": "base",
      "token": "USDC",
      "maxAmount": "1000000",
      "recipient": "0xabc...def",
      "resource": "https://api.nexflowapp.app/api/v1/metered/ep_xxx"
    }]
  }
}`}</code>
                  </pre>
                </div>

                <h2 className="text-xl font-semibold text-white mt-8 mb-4">Successful Request</h2>
                <div className="rounded-lg overflow-hidden bg-gray-900 border border-gray-800">
                  <pre className="p-4 overflow-x-auto text-sm">
                    <code className="text-gray-300 font-mono">{`curl https://api.nexflowapp.app/api/v1/metered/ep_xxx \\
  -H "Authorization: Bearer sk_..." \\
  -H "x-payment: 0x7f8a...3b2c" \\
  -d '{"prompt": "..."}'

# 200 OK - Payment verified, data returned`}</code>
                  </pre>
                </div>
              </div>
            </div>
          )}

          {activeSection === 'errors' && (
            <div>
              <h1 className="text-3xl font-bold mb-6">Errors</h1>
              
              <div className="prose prose-invert max-w-none">
                <p className="text-gray-400 mb-6">
                  NexFlow uses standard HTTP status codes and returns detailed error information.
                </p>

                <h2 className="text-xl font-semibold text-white mt-8 mb-4">HTTP Status Codes</h2>
                <div className="space-y-3">
                  {[
                    { code: '200', label: 'OK', desc: 'Request succeeded' },
                    { code: '400', label: 'Bad Request', desc: 'Invalid request parameters' },
                    { code: '401', label: 'Unauthorized', desc: 'Invalid or missing API key' },
                    { code: '402', label: 'Payment Required', desc: 'x402 challenge - payment needed' },
                    { code: '404', label: 'Not Found', desc: 'Resource not found' },
                    { code: '429', label: 'Rate Limited', desc: 'Too many requests' },
                    { code: '500', label: 'Server Error', desc: 'Internal server error' },
                  ].map(item => (
                    <div key={item.code} className="p-4 rounded-lg bg-gray-900 border border-gray-800 flex items-center gap-4">
                      <span className={`px-2 py-1 text-xs rounded font-mono ${
                        item.code === '200' ? 'bg-lime-400/20 text-lime-400' :
                        item.code === '402' ? 'bg-amber-500/20 text-amber-400' :
                        item.code.startsWith('4') ? 'bg-red-500/20 text-red-400' :
                        'bg-gray-700 text-gray-300'
                      }`}>{item.code}</span>
                      <div>
                        <span className="text-white font-medium">{item.label}</span>
                        <span className="text-gray-500 text-sm ml-2">{item.desc}</span>
                      </div>
                    </div>
                  ))}
                </div>

                <h2 className="text-xl font-semibold text-white mt-8 mb-4">Error Response Format</h2>
                <div className="rounded-lg overflow-hidden bg-gray-900 border border-gray-800">
                  <pre className="p-4 overflow-x-auto text-sm">
                    <code className="text-gray-300 font-mono">{`{
  "error": {
    "code": "invalid_payment",
    "message": "Payment verification failed",
    "details": {
      "reason": "tx_not_found",
      "facilitator": "cdp"
    }
  }
}`}</code>
                  </pre>
                </div>
              </div>
            </div>
          )}
        </main>
      </div>
    </div>
  )
}

export default function DocsPage() {
  return (
    <Suspense fallback={
      <div className="min-h-screen bg-gray-950 text-white flex items-center justify-center">
        <div className="text-center">
          <p className="text-gray-400">Loading docs...</p>
        </div>
      </div>
    }>
      <DocsPageContent />
    </Suspense>
  )
}
